/**
 * Elements
 */
body {
    @include prefixed(box-sizing, border-box);
    margin: 0;
    position: relative;
}

nav {
    position: absolute;
    top: 0;
    right: 40px;
    background: #FFF;

    li {
        list-style: none;
        float: left;
        margin: 10px 0;
        margin-right: 30px;
    }
}

h1 {
    font-size: 1.4em;
    text-align: center;
    margin: 50px 0;
}